.add {
  margin-top: 30px;
  padding: 25px;
  background-color: #3d5a80;
  border-radius: 4px;
  box-shadow: 15px 15px 30px rgba(0, 0, 0, 0.15);
  color: #fff;
}

.addForm {
  margin-top: 20px;
  display: flex;
}

.addForm input {
  width: 350px;
  margin-right: 20px;
}

@media (max-width: 600px) {
  .addForm {
    flex-direction: column;
    align-items: stretch;
  }

  .addForm input {
    width: 100%;
    margin-right: 0;
    margin-bottom: 10px;
  }

  .addForm button {
    width: 100%;
  }
}
